
import MasterPage from '../componentes/Master';
import Precio from '../componentes/Precio';
import fetch from 'isomorphic-unfetch';
import Eventos from '../componentes/Eventos';
import Noticias from '../componentes/Noticias';
import Axios from 'axios';
const Index = (props) =>(
    <MasterPage>
    <div className="row"> 
      <div className="col-12">
         <h2>Precio del Bitcoin</h2>
         <Precio
             precio={props.precioBitcoin}
         />
      </div>
      <div className="col-md-8">
         <h2 className="my-3">Noticias sobre el Bitcoin</h2>
         <Noticias 
              noticias={props.noticias}
         />
      </div>
      <div className="col-m-4">
         <h2 className="my-3">Pr√≥ximos Eventos Bitcoin</h2>
           {console.log(props.eventos)}
      </div>
    </div>
    </MasterPage>
);
Index.getInitialProps = async() => {
    const precio= await fetch('https://api.coinmarketcap.com/v2/ticker/1/');
    const noticias= await fetch('https://newsapi.org/v2/everything?q=bitcoin&sortBy=publishedAt&apiKey=3084f0a6cb6f4026b27f8ecd1ee1a247&languaje=es');
    token='W7VR7QBLFJMW5BHX2365';
    const eventos= await fetch(`https://www.eventbriteapi.com/v3/events/search/?q=bitcoin&sort_by=date&token=${this.token}&locale=es_ES`);
    
    const resEventos = await eventos.json();
    const resPrecio = await precio.json();
    const resNoticias = await noticias.json();

     return {
        precioBitcoin: resPrecio.data.quotes.USD,
        noticias: resNoticias.articles,
        eventos: resEventos.events
    }
}

export default Index;